 dass die personenbezogenen\n            Daten ohne Hinzuziehung zustzlicher Informationen nicht mehr einer spezifischen betroffenen Person\n            zugeordnet werden knnen, sofern diese zustzlichen Informationen gesondert aufbewahrt werden und\n            technischen und organisatorischen Manahmen unterliegen, die gewhrleisten, dass die personenbezogenen Daten\n            nicht einer identifizierten oder identifizierbaren natrlichen Person zugewiesen werden."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Profiling jede Art der automatisierten Verarbeitung personenbezogener Daten, die darin besteht, dass diese\n            personenbezogenen Daten verwendet werden, um bestimmte persnliche Aspekte, die sich auf eine natrliche\n            Person beziehen, zu bewerten, insbesondere um Aspekte bezglich Arbeitsleistung, wirtschaftliche Lage,\n            Gesundheit, persnliche Vorlieben, Interessen, Zuverlssigkeit, Verhalten, Aufenthaltsort oder Ortswechsel\n            dieser natrlichen Person zu analysieren oder vorherzusagen."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Als Verantwortlicher wird die natrliche oder juristische Person, Behrde, Einrichtung oder andere Stelle,\n            die allein oder gemeinsam mit anderen ber die Zwecke und Mittel der Verarbeitung von personenbezogenen\n            Daten entscheidet, bezeichnet."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Auftragsverarbeiter eine natrliche oder juristische Person, Behrde, Einrichtung oder andere Stelle, die\n            personenbezogene Daten im Auftrag des Verantwortlichen verarbeitet."),_c('br')]),_vm._v(" "),_c('h3',{attrs:{"id":"dsg-general-legalbasis"}},[_vm._v("Magebliche Rechtsgrundlagen")]),_vm._v(" "),_c('p',[_vm._v("Nach Magabe des Art. 13 DSGVO teilen wir Ihnen die Rechtsgrundlagen unserer Datenverarbeitungen mit. Fr\n            Nutzer aus dem Geltungsbereich der Datenschutzgrundverordnung (DSGVO), d.h. der EU und des EWG gilt, sofern\n            die Recht