sten\n            werden die personenbezogenen Cookies der Nutzer entsprechend den nachfolgenden Erluterungen im Rahmen\n            dieser Datenschutzerklrung auf Grundlage unserer berechtigten Interessen (d.h. Interesse an der Analyse,\n            Optimierung und wirtschaftlichem Betrieb unseres Onlineangebotes im Sinne des Art. 6 Abs. 1 lit. f. DSGVO)\n            oder sofern der Einsatz von Cookies zur Erbringung unserer vertragsbezogenen Leistungen erforderlich ist,\n            gem. Art. 6 Abs. 1 lit. b. DSGVO, bzw. sofern der Einsatz von Cookies fr die Wahrnehmung einer Aufgabe, die\n            im ffentlichen Interesse liegt erforderlich ist oder in Ausbung ffentlicher Gewalt erfolgt, gem. Art. 6\n            Abs. 1 lit. e. DSGVO, verarbeitet."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Falls die Nutzer nicht mchten, dass Cookies auf ihrem Rechner gespeichert werden, werden sie gebeten die\n            entsprechende Option in den Systemeinstellungen ihres Browsers zu deaktivieren. Gespeicherte Cookies knnen\n            in den Systemeinstellungen des Browsers gelscht werden. Der Ausschluss von Cookies kann zu\n            Funktionseinschrnkungen dieses Onlineangebotes fhren."),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n            Ein genereller Widerspruch gegen den Einsatz der zu Zwecken des Onlinemarketing eingesetzten Cookies kann\n            bei einer Vielzahl der Dienste, vor allem im Fall des Trackings, ber die US-amerikanische Seite "),_c('a',{attrs:{"href":"http://www.aboutads.info/choices/"}},[_vm._v("http://www.aboutads.info/choices/")]),_vm._v(" oder die EU-Seite "),_c('a',{attrs:{"href":"http://www.youronlinechoices.com/"}},[_vm._v("http://www.youronlinechoices.com/")]),_vm._v(" erklrt werden. Des\n            Weiteren kann die Speicherung von Cookies mittels deren Abschaltung in den Einstellungen des Browsers\n            erreicht werden. Bitte beachten Sie, dass dann gegebenenfalls nicht alle Funktionen dieses Onlineangebotes\n       